.SectionTwoImg {
    width: 100%;
}
.SectionTwoA {
    position: relative;
    display: block;
}
.SectionTwoA::before {
    bottom: 10px;
    content: "";
    left: 10px;
    opacity: 0;
    position: absolute;
    right: 10px;
    top: 10px;
    transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
    z-index: 10000;
    border-bottom: 1px solid rgb(255, 255, 255);
    border-top: 1px solid rgb(255, 255, 255);
    transform: scale(0, 1);
}
.SectionTwoA::after {
    bottom: 10px;
    content: "";
    left: 10px;
    opacity: 0;
    position: absolute;
    right: 10px;
    top: 10px;
    transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
    z-index: 10000;
    border-left: 1px solid rgb(255, 255, 255);
    border-right: 1px solid rgb(255, 255, 255);
    transform: scale(1, 0);
}
.SectionTwoA:hover::before, .SectionTwoA:hover::after {
    opacity: 1;
    transform: scale(1);
}
.SectionTwoVins a {
    text-align: center;
}
.SectionTwoVins a img {
    opacity: 0.5;
    width: 50%;
}
.SectionTwoVins a img:hover {
    opacity: 1;
}
.SectionTwoVins {
    padding-bottom: 20px;
}